<template>
    <div id="app" @touchstart.stop="handleTap">
        <transition name="slide">
            <keep-alive>
                <router-view v-if="$route.meta.keepAlive"></router-view>
            </keep-alive>
        </transition>
        <transition name="slide">
            <router-view v-if="!$route.meta.keepAlive"></router-view>
        </transition>
        <template v-if="showH5Tips">
            <div class="user-h5-tips">
                <div class="inner">
                    <div class="close">
                        <span
                            @click.stop="showH5Tips = false"
                            class="iconfont icon-xianshi_quxiaotianchong"
                        ></span>
                    </div>
                    <div class="detail-h5">
                        <p>推荐使用h5页面</p>
                        <p>您的设备屏幕尺寸过小.</p>
                    </div>
                    <div class="btn">
                        <a href="/public/h5.html">打开</a>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    data() {
        return {
            showH5Tips: true
        };
    },
    methods: {
        //防止移动端hover事件触摸后不隐藏
        handleTap(e) {
            e.target.dataset.type === "header-img"
                ? this.$store.commit("HIDENAV", false)
                : this.$store.commit("HIDENAV", true);
        }
    }
};
</script>

<style lang="scss">
#app {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
        "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    color: $uni-text-color;
    position: relative;
    width: 100%;
    height: 100%;
    @extend .transiton-slide; //路由动画
    @extend .transiton-fade; //路由动画
    @extend .transiton-nav-show; //nav
    @media screen and(max-width: 500px) {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }

    .user-h5-tips {
        width: 100%;
        height: 50px;
        background-color: $uni-bg-color-grey;
        position: fixed;
        display: none;
        bottom: 0;
        overflow: hidden;
        left: 0;
        z-index: 999;

        .inner {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: row;
            line-height: 50px;
            box-sizing: border-box;
            padding: 0 1rem;
            text-align: center;
            justify-content: center;
            align-items: center;

            div.close {
                width: 20%;
                color: $uni-color-warning;
                .iconfont {
                    font-size: 20px;
                }
            }

            div.detail-h5 {
                width: 100%;
                font-size: 16px;
                & > p {
                    height: 20px;
                    line-height: 16px;
                    text-align: left;
                    color: $uni-text-color;
                    &:last-of-type {
                        font-size: 14px;
                        color: $uni-color-warning;
                    }
                }
            }
            .btn {
                width: 100px;
                a {
                    width: 100px;
                    color: $uni-color-primary;
                    padding: 0.25rem;
                }
            }
        }
    }
    @media screen and(max-width: 750px) {
        .user-h5-tips {
            display: block;
        }
    }
}

body {
    position: relative;
    width: 100%;
    height: 100%;
}
</style>
